import request from '@/utils/request'

// 获取所有菜单
export const getAllMenus = () => {
  return request({
    method: 'get',
    url: '/boss/menu/getAll'
  })
}

// 按条件分页查询菜单
export const getMenuPages = data => {
  return request({
    method: 'post',
    url: '/boss/menu/getMenuPages',
    data
  })
}

// 获取编辑菜单页面信息
export const getEditMenuInfo = (menuId = -1) => {
  return request({
    method: 'get',
    url: '/boss/menu/getEditMenuInfo',
    params: {
      id: menuId
    }
  })
}

// 保存或新增菜单
export const createOrUpdateMenu = data => {
  return request({
    method: 'post',
    url: '/boss/menu/saveOrUpdate',
    data
  })
}

// 删除菜单
export const deleteMenu = menuId => {
  return request({
    method: 'delete',
    url: `/boss/menu/${menuId}`
  })
}

// 获取所有菜单并按层级展示
export const getMenuNodeList = () => {
  return request({
    method: 'get',
    url: '/boss/menu/getMenuNodeList'
  })
}

// 给角色分配菜单
export const allocateRoleMenus = data => {
  return request({
    method: 'post',
    url: '/boss/menu/allocateRoleMenus',
    data
  })
}

// 获取角色拥有的菜单列表
export const getRoleMenus = roleId => {
  return request({
    method: 'get',
    url: '/boss/menu/getRoleMenus',
    params: {
      roleId
    }
  })
}
