<template>
  <div class="menu-create-or-edit">
    <!-- 页头（返回） -->
    <el-page-header
      :content="isEdit ? '编辑菜单' : '添加菜单'"
      @back="goBack"></el-page-header>
    <!-- 菜单表单 -->
    <el-card
      shadow="never">
      <div slot="header">
        <span>{{ isEdit ? '编辑菜单' : '添加菜单' }}</span>
      </div>
      <el-form
        ref="menu-form"
        :model="menuForm"
        :rules="menuFormRules"
        label-width="80px">
        <el-form-item
          label="菜单名称"
          prop="name">
          <el-input
            v-model="menuForm.name"></el-input>
        </el-form-item>
        <el-form-item
          label="菜单路径">
          <el-input
            v-model="menuForm.href"></el-input>
        </el-form-item>
        <el-form-item
          label="上级菜单">
          <el-select
            v-model="menuForm.parentId"
            placeholder="请选择上级菜单">
            <el-option
              label="无上级菜单"
              :value="-1"
            ></el-option>
            <el-option
              v-for="item in parentMenuList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
              ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="描述"
          prop="description" >
          <el-input
            v-model="menuForm.description"></el-input>
        </el-form-item>
        <el-form-item
          label="前端图标"
          prop="icon" >
          <el-input
            v-model="menuForm.icon"></el-input>
        </el-form-item>
        <el-form-item
          label="是否显式">
          <el-radio-group
            v-model="menuForm.shown">
            <el-radio
              :label="true">是</el-radio>
            <el-radio
              :label="false">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          label="排序">
          <el-input-number
            v-model="menuForm.orderNum"
            label="描述文字"></el-input-number>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="onSubmit">提交</el-button>
          <el-button
            v-if="!isEdit">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import {
  getEditMenuInfo,
  createOrUpdateMenu
} from '@/services/menu'

export default {
  name: 'CreateOrEdit',
  props: {
    isEdit: {
      type: Boolean,
      default: false
    },
    menuId: {
      type: [Number, String],
      default: -1
    }
  },
  data () {
    return {
      menuForm: {
        name: '',
        href: '',
        parentId: -1,
        description: '',
        icon: '',
        shown: true,
        orderNum: 0
      },
      parentMenuList: [],
      menuFormRules: {
        name: [
          { required: true, message: '请输入菜单名称', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '请输入菜单描述', trigger: 'blur' }
        ],
        icon: [
          { required: true, message: '请输入前端图标', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.loadMenuInfo()
  },
  methods: {
    async loadMenuInfo () {
      const { data } = await getEditMenuInfo(this.menuId)
      if (data.code === '000000') {
        this.parentMenuList = data.data.parentMenuList
        if (data.data.menuInfo) {
          this.menuForm = data.data.menuInfo
        }
      }
    },
    async onSubmit () {
      try {
        await this.$refs['menu-form'].validate()
        const { data } = await createOrUpdateMenu(this.menuForm)
        if (data.code === '000000') {
          this.$message.success('提交成功')
          this.$router.push({
            name: 'menu'
          })
        }
      } catch (error) {
        console.log('表单验证失败')
      }
    },
    goBack () {
      this.$router.back()
    }
  }
}
</script>

<style lang="scss" scoped>
.menu-create-or-edit {
  .el-page-header {
    margin-bottom: 20px;
  }
}
</style>
